import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Link } from "react-router-dom";

class SelectEventoLive extends Component {
    render() {
        let meeting_center = "";

        let meeting_desc = "";

        let event = null;
        if (this.props.events) {
            event = this.props.events.findIndex(x => x.id === parseInt(this.props.match.params.id));
            event = this.props.events[event];
            meeting_desc = event.meeting_desc;
        }

        if (this.props.meeting_center) {
            meeting_center = this.props.meeting_center.map(key =>
                parseInt(key.evento_id) === parseInt(this.props.match.params.id) ?
                    (
                        <li key={key.id}
                            id={key.id}>
                            <div className="place">
                                {key.lugar}
                            </div>
                            <div className="date">
                                {key.fecha} {key.hora}
                            </div>
                        </li>
                    ) : ""
            );
        }
        return (
            <div className="asistencia-container">
                <div className="submenu">
                    <div className={`ham`}>
                        <Link to={`/detail/${this.props.match.params.id}`}>
                            <FontAwesomeIcon icon="chevron-circle-left" />
                        </Link>
                    </div>
                </div>
                <div className="asistencia nobg">
                    <h1>Meeting Center</h1>
                    <div className="description" dangerouslySetInnerHTML={{ __html: meeting_desc }} />
                    <ul className="meetings">
                        {meeting_center}
                    </ul>
                </div>
            </div>
        );
    }
}
export default SelectEventoLive;