import React, { Component } from 'react';
import { Redirect } from 'react-router'
import { Link } from "react-router-dom";
import axios from 'axios';
import swal from 'sweetalert';

class Forgot extends Component {

    constructor(props) {
        super(props);
        this.forgot = this.forgot.bind(this);
        this.email = React.createRef();
    }

    forgot() {
        if (this.email.current.value === "") {
            swal("Error", "Please fill in the email", "error");
        } else {
            //Login
            axios.post(this.props.url + "api/forgot",
                {
                    email: this.email.current.value,
                })
                .then(function (response) {
                    if (response.data.error === 0) {
                        swal("Success", "We have sent your password to your email.", "success");
                    } else {
                        swal("Error", response.data.message, "error");
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
        }
    }

    render() {
        return (
            <div className="login-container">
                {
                    this.props.logged ? (<Redirect to="/home" />) : ""
                }
                <div className="form">
                    <div className="header">
                        Forgot Password
                    </div>
                    <div className="row second">
                        <input type="text" name="email" ref={this.email} placeholder="Email" />
                    </div>
                    <div className="row buttons">
                        <Link to={`/`} className="forgot">
                            Ingresar
                        </Link>
                        <button className="create" type="button" onClick={this.forgot}>
                            SEND PASSWORD
                        </button>
                    </div>
                </div>

            </div >
        );
    }
}
export default Forgot;