import React, { Component } from 'react';
import { Link } from "react-router-dom";

class Home extends Component {
    render() {
        let events = "";
        if (this.props.events) {
            events = this.props.events.map(key => (
                <li key={key.id}
                    id={key.id}>
                    <Link to={`/detail/${key.id}`}>
                        <img src={this.props.img_url + key.baner} alt={key.nombre} />
                        <div className="info">
                            <span className="date">
                                {key.fecha_inicio}
                            </span>
                            <span className="name">
                                {key.nombre}
                            </span>
                            <span className="place">
                                {key.lugar}
                            </span>
                        </div>
                    </Link>
                </li>
            ));
        }
        return (
            <div className="asistencia-container">
                <div className="asistencia">
                    <ul className="eventos">
                        {events}
                    </ul>
                </div>
            </div>
        );
    }
}
export default Home;