import React, { Component } from 'react';
import { Redirect } from 'react-router'
import { Link } from "react-router-dom";
import axios from 'axios';
import swal from 'sweetalert';

class Login extends Component {

    constructor(props) {
        super(props);
        this.login = this.login.bind(this);
        this.state = {
            active: 0
        };
        this.email = React.createRef();
        this.zip = React.createRef();
        this.password = React.createRef();
    }

    login() {
        if (this.email.current.value === "" || this.password.current.value === "") {
            swal("Error", "Por favor Ingrese email y contraseña", "error");
        } else {

            let t = this;
            //Login
            axios.post(this.props.url + "api/login-mobile",
                {
                    email: this.email.current.value,
                    password: this.password.current.value,
                })
                .then(function (response) {
                    if (response.data.error === 0) {
                        localStorage.setItem("estrategoID", response.data.id);
                        localStorage.setItem("estrategoName", response.data.name);
                        t.props.changeLogged(true);
                        t.props.changeCurrent("home");
                        t.props.getMeetingCenter();
                    } else {
                        swal("Error", response.data.message, "error");
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
        }
    }

    render() {
        return (
            <div className="login-container">
                {
                    this.props.logged ? (<Redirect to="/home" />) : ""
                }
                <div className="form">
                    <div className="header">
                        Bienvenido
                    </div>
                    <div className="row">
                        <input type="email" name="email" ref={this.email} placeholder="Email" />
                    </div>
                    <div className="row">
                        <input type="password" name="password" ref={this.password} placeholder="Contraseña" />
                    </div>
                    <div className="row buttons">
                        <Link to={`/forgot`} className="forgot">
                            Olvidé mi Contraseña
                        </Link>
                        <button className="create" type="button" onClick={this.login}>
                            LOGIN
                        </button>
                    </div>
                </div>

            </div >
        );
    }
}
export default Login;