import React, { Component } from 'react';
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

class Header extends Component {
    constructor(props) {
        super(props);
        this.changeMenuStatus = this.changeMenuStatus.bind(this);
        this.state = {
            showMenu: false
        };
    }

    // Change the menu status
    changeMenuStatus() {
        this.setState({ showMenu: !this.state.showMenu });
    }

    render() {

        // We check if the user is logged in
        if (!this.props.isLoggedIn) {
            return (
                ""
            );
        }

        let menuActive = this.state.showMenu ? "active" : "";

        //If it's logged in we display the menu
        return (
            <header>
                <div className="brand">
                    EM
                </div>
                <ul className={menuActive}>
                    <li>
                        <Link className={this.props.current === "eventos" ? "active" : ""} to="/home" onClick={() => { this.props.changeCurrent("asistencia"); this.changeMenuStatus(); }}>
                            <FontAwesomeIcon icon="calendar" />
                            Eventos
                        </Link>
                    </li>
                    <li>
                        <Link className="sign-out" to="/landing" onClick={() => { this.changeMenuStatus(); this.props.changeLogged(false); this.props.logOff(); }}>
                            <FontAwesomeIcon icon="sign-out" />
                            Salir
                        </Link>
                    </li>
                </ul>
                <div className={`ham ${menuActive}`} onClick={this.changeMenuStatus}>
                    <FontAwesomeIcon icon="bars" />
                </div>

            </header >
        );
    }
}
export default Header;