import React, { Component } from 'react';
import { HashRouter as Router, Route } from "react-router-dom";

// Custom Components
import Header from './components/Header';
import Home from './components/Home';
import SelectEventoLive from './components/SelectEventoLive';
import Detail from './components/Detail';
import SelectEventoInvitados from './components/SelectEventoInvitados';
import Live from './components/Live';
import Invitados from './components/Invitados';
import Login from './components/Login';
import Forgot from './components/Forgot';
import axios from 'axios';
// We import the css
import './css/App.css';

// Fontawesome
import { library } from '@fortawesome/fontawesome-svg-core';
import { faBars, faPlay, faNewspaper, faMoneyCheck, faPrint, faEnvelope, faSignIn, faClipboardList, faChartLine, faSignOut, faCalendar, faHandshakeAlt, faInfoCircle, faQuestion, faTools, faMegaphone, faChevronCircleLeft } from '@fortawesome/pro-solid-svg-icons';
library.add(faBars, faPlay, faNewspaper, faMoneyCheck, faPrint, faEnvelope, faSignIn, faClipboardList, faChartLine, faSignOut, faBars, faCalendar, faHandshakeAlt, faInfoCircle, faQuestion, faTools, faMegaphone, faChevronCircleLeft);

let url = "http://192.241.219.113/estratego/public/";
let img_url = "http://192.241.219.113/estratego/public/";

class App extends Component {
  constructor(props) {
    super(props);
    this.changeLogged = this.changeLogged.bind(this);
    this.changeCurrent = this.changeCurrent.bind(this);
    this.getMeetingCenter = this.getMeetingCenter.bind(this);
    this.logOff = this.logOff.bind(this);

    this.state = {
      logged: false,
      windowTitle: "Bienvenido",
      events: null,
      empresas: null,
      cargos: null,
      puestos: null,
      current: "eventos",
      meeting_center: null
    };
  }

  logOff() {
    this.changeCurrent("eventos");
    localStorage.removeItem("estrategoID");
  }

  getMeetingCenter() {
    let t = this;
    let user = localStorage.getItem("estrategoID");
    // Get the events
    axios.post(url + "api/get-meeting-center", { user })
      .then(function (response) {
        t.setState({ meeting_center: response.data });
      })
      .catch(function (error) {
        console.log(error);
      });
  }

  componentDidMount() {
    let t = this;
    // We set the logged in
    let id = localStorage.getItem("estrategoID");

    if (id) {
      this.changeLogged(true);
      this.getMeetingCenter();
    }
    // Get the events
    axios.post(url + "api/get-events")
      .then(function (response) {
        t.setState({ events: response.data });
      })
      .catch(function (error) {
        console.log(error);
      });
    // Get the cargos
    axios.post(url + "api/get-cargo-generico")
      .then(function (response) {
        t.setState({ cargos: response.data });
      })
      .catch(function (error) {
        console.log(error);
      });
    // Get the puestos
    axios.post(url + "api/get-puestos")
      .then(function (response) {
        t.setState({ puestos: response.data });
      })
      .catch(function (error) {
        console.log(error);
      });
    // Get the empresas
    axios.post(url + "api/get-empresas")
      .then(function (response) {
        t.setState({ empresas: response.data });
      })
      .catch(function (error) {
        console.log(error);
      });
  }

  //Function to change the logged state
  changeLogged(logged) {
    this.setState({ logged });
  }

  changeCurrent(current) {
    this.setState({ current });
  }


  getUserData(id) {
    let t = this;
    let deviceID = localStorage.getItem("estID");
    // Get the user data
    axios.post(url + "api/get-user-info", {
      user: id,
      deviceID
    })
      .then(function (response) {
        t.setState({ userData: response.data[0], count: t.state.count - 1 });
      })
      .catch(function (error) {
        console.log(error);
      });
  }

  render() {
    return (
      <Router>
        <div id="main">
          <Header
            isLoggedIn={this.state.logged}
            title={this.state.windowTitle}
            changetitle={this.changetitle}
            changeLogged={this.changeLogged}
            setUserInfo={this.setUserInfo}
            guest={this.state.guest}
            changeGuest={this.changeGuest}
            current={this.state.current}
            changeCurrent={this.changeCurrent}
            logOff={this.logOff}
          />
          {
            !this.state.logged ?
              (
                <div className="routes-container">
                  <Route path="/forgot"
                    render={(props) =>
                      <Forgot
                        {...props}
                        url={url}
                        img_url={img_url}
                        events={this.state.events}
                      />} />

                  <Route path="/"
                    render={(props) =>
                      <Login
                        {...props}
                        url={url}
                        logged={this.state.logged}
                        changeLogged={this.changeLogged}
                        changeCurrent={this.changeCurrent}
                        getMeetingCenter={this.getMeetingCenter}
                      />} />
                </div>

              )
              :
              (
                <div className="routes-container">
                  <Route path="/landing"
                    render={(props) => <Login {...props} url={url} logged={this.state.logged} changeLogged={this.changeLogged} setUserInfo={this.setUserInfo} changeGuest={this.changeGuest} />} />

                  <Route exact path="/"
                    render={(props) =>
                      <Home
                        {...props}
                        url={url}
                        img_url={img_url}
                        events={this.state.events}
                      />} />

                  <Route path="/home"
                    render={(props) =>
                      <Home
                        {...props}
                        url={url}
                        img_url={img_url}
                        events={this.state.events}
                      />} />

                  <Route path="/detail/:id"
                    render={(props) =>
                      <Detail
                        {...props}
                        url={url}
                        img_url={img_url}
                        events={this.state.events}
                        cargos={this.state.cargos}
                        puestos={this.state.puestos}
                        empresas={this.state.empresas}
                      />} />

                  <Route path="/meeting-center/:id"
                    render={(props) =>
                      <SelectEventoLive
                        {...props}
                        url={url}
                        img_url={img_url}
                        events={this.state.events}
                        meeting_center={this.state.meeting_center}
                      />} />

                  <Route path="/select-evento-invitados"
                    render={(props) =>
                      <SelectEventoInvitados
                        {...props}
                        url={url}
                        img_url={img_url}
                        events={this.state.events}
                      />} />

                  <Route path="/invitados/:id"
                    render={(props) =>
                      <Invitados
                        {...props}
                        url={url}
                        img_url={img_url}
                      />} />

                  <Route path="/live/:id"
                    render={(props) =>
                      <Live
                        {...props}
                        url={url}
                        img_url={img_url}
                      />} />

                </div>
              )}
        </div>
      </Router>
    );
  }
}

export default App;
