import React, { Component } from 'react';
import axios from 'axios';
import {
    BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, LineChart, Line, ResponsiveContainer
} from 'recharts';

class Live extends Component {
    constructor(props) {
        super(props);
        this.getInfo = this.getInfo.bind(this);

        this.state = {
            info: null,
            hour: null
        }

    }

    componentDidMount() {
        this.getInfo();
    }
    componentDidUpdate(prevProps) {
        if (prevProps.match.params.id !== this.props.match.params.id) {
            this.getInfo()
        }
    }

    //############################################################
    getInfo() {
        let t = this;
        // Get the events
        axios.post(this.props.url + "api/get-attendance", {
            event: this.props.match.params.id
        })
            .then(function (response) {
                let info = [response.data];
                t.setState({ info });

                // window.setTimeout(t.getInfo, 5000);
            })
            .catch(function (error) {
                console.log(error);
            });
        // Get the events by half an hour
        axios.post(this.props.url + "api/get-attendance-hour", {
            event: this.props.match.params.id
        })
            .then(function (response) {
                t.setState({ hour: response.data });

                window.setTimeout(t.getInfo, 5000);
            })
            .catch(function (error) {
                console.log(error);
            });
    }

    render() {

        return (
            <div className="asistencia-container">
                <div className="asistencia">
                    <div className="row2">
                        <h1>
                            Asistencia Tiempo Real
                            </h1>
                        {this.state.info ?
                            (
                                <ResponsiveContainer width="100%" height={400}>
                                    <BarChart
                                        data={this.state.info}
                                        margin={{
                                            top: 5, right: 30, left: 20, bottom: 5,
                                        }}
                                    >
                                        <CartesianGrid strokeDasharray="3 3" />
                                        <XAxis dataKey="name" />
                                        <YAxis />
                                        <Tooltip />
                                        <Legend />
                                        <Bar label={{ fill: 'white' }} dataKey="total" fill="#3D3D3D" />
                                        <Bar label={{ fill: 'white' }} dataKey="si" fill="#009EE3" />
                                        <Bar label={{ fill: 'white' }} dataKey="no" fill="#E50041" />
                                    </BarChart>
                                </ResponsiveContainer>) : ""
                        }
                    </div>
                    <div className="row2">
                        <h1>
                            Asistencia Por Hora
                        </h1>
                        {this.state.hour ?
                            (
                                <ResponsiveContainer width="100%" height={300}>
                                    <LineChart
                                        data={this.state.hour}
                                        margin={{
                                            top: 5, right: 30, left: 20, bottom: 5,
                                        }}
                                    >
                                        <CartesianGrid strokeDasharray="3 3" />
                                        <XAxis dataKey="period" />
                                        <YAxis />
                                        <Tooltip />
                                        <Legend />
                                        <Line type="monotone" dataKey="cantidad" stroke="#8884d8" activeDot={{ r: 8 }} />
                                    </LineChart>
                                </ResponsiveContainer>) : ""
                        }
                    </div>
                </div>
            </div>
        );
    }
}
export default Live;