import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Link } from "react-router-dom";
class Detail extends Component {
    constructor(props) {
        super(props);
        this.changeMenuStatus = this.changeMenuStatus.bind(this);
        this.changeCurrent = this.changeCurrent.bind(this);
        this.state = {
            showMenu: false,
            current: ""
        };

        this.start = React.createRef();
        this.agenda = React.createRef();
        this.press = React.createRef();
        this.sponsors = React.createRef();
        this.faqs = React.createRef();
        this.show = React.createRef();
        this.speaker = React.createRef();

    }

    scrollTo(ref) {
        window.scrollTo(0, ref.current.offsetTop - 100);
    }

    changeMenuStatus() {
        this.setState({ showMenu: !this.state.showMenu });
    }
    changeCurrent(current) {
        this.setState({ current });
    }

    render() {
        let menuActive = this.state.showMenu ? "active" : "";

        let content = "";
        let event = null;
        if (this.props.events) {
            event = this.props.events.findIndex(x => x.id === parseInt(this.props.match.params.id));
            event = this.props.events[event];

            content = (
                <div className="detail-container">
                    <div className="title">
                        {event.nombre}
                    </div>
                    {
                        event.inicio !== "" && event.inicio !== null ?
                            (
                                <div id="inicio" className="imagen" ref={this.start}>
                                    <img src={this.props.url + "/" + event.inicio} alt="Inicio" />
                                </div>
                            ) : ""
                    }
                    {
                        event.agenda !== "" && event.agenda !== null ?
                            (
                                <div id="agenda" className="imagen" ref={this.agenda}>
                                    <img src={this.props.url + "/" + event.agenda} alt="agenda" />
                                </div>
                            ) : ""
                    }
                    {
                        event.nota_prensa !== "" && event.nota_prensa !== null ?
                            (
                                <div id="nota_prensa" className="imagen" ref={this.press}>
                                    <img src={this.props.url + "/" + event.nota_prensa} alt="nota de prensa" />
                                </div>
                            ) : ""
                    }
                    {
                        event.patrocinadores !== "" && event.patrocinadores !== null ?
                            (
                                <div id="patrocinadores" className="imagen" ref={this.sponsors}>
                                    <img src={this.props.url + "/" + event.patrocinadores} alt="patrocinadores" />
                                </div>
                            ) : ""
                    }
                    {
                        event.faqs !== "" && event.faqs !== null ?
                            (
                                <div id="faqs" className="imagen" ref={this.faqs}>
                                    <img src={this.props.url + "/" + event.faqs} alt="faqs" />
                                </div>
                            ) : ""
                    }
                    {
                        event.show_floor !== "" && event.show_floor !== null ?
                            (
                                <div id="show_floor" className="imagen" ref={this.show}>
                                    <img src={this.props.url + "/" + event.show_floor} alt="show_floor" />
                                </div>
                            ) : ""
                    }
                    {
                        event.speakers !== "" && event.speakers !== null ?
                            (
                                <div id="speakers" className="imagen" ref={this.speaker}>
                                    <img src={this.props.url + "/" + event.speakers} alt="speakers" />
                                </div>
                            ) : ""
                    }
                </div>
            );




        }

        return (
            <React.Fragment>
                <div className="submenu">
                    <div className={`ham ${menuActive}`} onClick={this.changeMenuStatus}>
                        <FontAwesomeIcon icon="bars" />
                    </div>
                    <ul className={menuActive}>
                        <li>
                            {
                                event && event.inicio !== "" && event.inicio !== null ?
                                    (
                                        <div className={this.current === "eventos" ? "active" : ""} onClick={() => { this.changeCurrent("asistencia"); this.changeMenuStatus(); this.scrollTo(this.start); }}>
                                            <FontAwesomeIcon icon="play" />
                                            Inicio
                                </div>
                                    ) : ""
                            }

                            {
                                event && event.agenda !== "" && event.agenda !== null ?
                                    (
                                        <div className={this.current === "eventos" ? "active" : ""} onClick={() => { this.changeCurrent("asistencia"); this.changeMenuStatus(); this.scrollTo(this.agenda); }}>
                                            <FontAwesomeIcon icon="calendar" />
                                            Agenda
                                        </div>
                                    ) : ""
                            }

                            {
                                event && event.nota_prensa !== "" && event.nota_prensa !== null ?
                                    (
                                        <div className={this.current === "eventos" ? "active" : ""} onClick={() => { this.changeCurrent("asistencia"); this.changeMenuStatus(); this.scrollTo(this.press); }}>
                                            <FontAwesomeIcon icon="newspaper" />
                                            Nota de Prensa
                                    </div>
                                    ) : ""
                            }

                            {
                                event && event.patrocinadores !== "" && event.patrocinadores !== null ?
                                    (
                                        <div className={this.current === "eventos" ? "active" : ""} onClick={() => { this.changeCurrent("asistencia"); this.changeMenuStatus(); this.scrollTo(this.sponsors); }}>
                                            <FontAwesomeIcon icon="money-check" />
                                            Patrocinadores
                                    </div>
                                    ) : ""
                            }

                            {
                                event && event.faqs !== "" && event.faqs !== null ?
                                    (
                                        <div className={this.current === "eventos" ? "active" : ""} onClick={() => { this.changeCurrent("asistencia"); this.changeMenuStatus(); this.scrollTo(this.faq); }}>
                                            <FontAwesomeIcon icon="question" />
                                            FAQs
                                        </div>
                                    ) : ""
                            }

                            {
                                event && event.show_floor !== "" && event.show_floor !== null ?
                                    (
                                        <div className={this.current === "eventos" ? "active" : ""} onClick={() => { this.changeCurrent("asistencia"); this.changeMenuStatus(); this.scrollTo(this.show); }}>
                                            <FontAwesomeIcon icon="tools" />
                                            Show Floor
                                        </div>
                                    ) : ""
                            }

                            {
                                event && event.speakers !== "" && event.speakers !== null ?
                                    (
                                        <div className={this.current === "eventos" ? "active" : ""} onClick={() => { this.changeCurrent("asistencia"); this.changeMenuStatus(); this.scrollTo(this.speaker); }}>
                                            <FontAwesomeIcon icon="megaphone" />
                                            Speakers
                                        </div>
                                    ) : ""
                            }

                            <div>
                                <Link className={this.props.current === "meeting-center" ? "active" : ""} to={`/meeting-center/${this.props.match.params.id}`} onClick={() => { this.changeMenuStatus(); }}>
                                    <FontAwesomeIcon icon="handshake-alt" />
                                    Meeting Center
                                </Link>
                            </div>
                        </li>
                    </ul>
                </div>
                {content}
            </React.Fragment>
        );
    }
}
export default Detail;