import React, { Component } from 'react';
import { Link } from "react-router-dom";

class SelectEventoInvitados extends Component {
    render() {
        let events = "";
        if (this.props.events) {
            events = this.props.events.map(key => (
                <li key={key.id}
                    id={key.id}>
                    <Link to={`/invitados/${key.id}`}>
                        <img src={this.props.img_url + key.baner} alt={key.nombre} />
                        <span className="name">
                            {key.nombre}
                        </span>
                    </Link>
                </li>
            ));
        }
        return (
            <div className="asistencia-container">
                <div className="asistencia">
                    <h1>Estadísticas</h1>
                    <ul className="eventos">
                        {events}
                    </ul>
                </div>
            </div>
        );
    }
}
export default SelectEventoInvitados;